import { Button, Card, CardActions, CardContent, CardMedia, Container, Typography } from "@mui/material";
import { Box } from "@mui/system";

const Proyectos = () => {

    const proyectos = [
        {
            nombre: 'App de Reseñas, CRUD.',
            descripcion: 'Aplicación de Reseñas en Stack MERN, creada para un ramo con la intención de crear una imagen de Docker para Frontend y Backend para luego usar Docker-Compose para levantar la aplicación, desplegado el Front en Netlify y Servidor ExpressJS en Heroku.',
            imagen: 'https://i.imgur.com/GmfObNR.jpg',
            enlace: 'https://xenodochial-villani-99100e.netlify.app/'
        },
        {
            nombre: 'Visualizador de Algoritmos',
            descripcion: 'Visualizador de Algoritmos de ordenamiento (MergeSort, BubbleSort, SelectionSort) hecho con React',
            imagen: 'https://i.imgur.com/EYM3LeQ.jpg',
            enlace: 'https://determined-babbage-98b325.netlify.app/'
        },
        {
            nombre: 'MERN Tasks',
            descripcion: 'Aplicación CRUD con autenticación para asignarle tareas a distintos proyectos y marcarlas como completas, hecha para curso de Stack MERN',
            imagen: 'https://i.imgur.com/KsVMC5N.jpg',
            enlace: 'https://quirky-darwin-9cfb5c.netlify.app/'
        },
        {
            nombre: 'React Gif-Expert-App',
            descripcion: 'Aplicación de React hecha para un curso de UDEMY con la intención de consumir una API pública',
            imagen: 'https://i.imgur.com/AEAUsmi.jpg',
            enlace: 'https://camiloerh.github.io/react-gif-expert-app/'
        },

    ]
    return (  
        <Container>
            <Box
                sx={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
                    gap: 2
                }}
            >
            {
                proyectos.map((proyecto) => (
                        <Card
                            key={proyecto.nombre}
                            sx={{ 
                                "&:hover": {
                                    boxShadow: 5
                                },
                                userSelect: 'none',
                                display: 'flex',
                                flexDirection: 'column'
                            }}
                                  
                        >
                            <CardMedia
                                component="img"
                                alt="imagen"
                                height="140"
                                image={proyecto.imagen}
                            />
                            <CardContent
                                sx={{
                                    flexGrow: 1
                                }}
                            >
                                <Typography  
                                    align="center" 
                                    gutterBottom 
                                    variant="h5" 
                                    component="div"
                                >
                                    {proyecto.nombre}
                                </Typography>
                                <Typography component="div" gutterBottom variant="body2" color="text.secondary">
                                    {proyecto.descripcion}
                                </Typography>
                            </CardContent>
                            <CardActions>
                                <Button
                                    component="a" 
                                    href={proyecto.enlace}
                                    size="small"
                                >
                                    Visitar Proyecto
                                </Button>
                            </CardActions>
                        </Card>
                ))
            }
            </Box>
        </Container>
    );
}
 
export default Proyectos;