import React from 'react'

import { ButtonBase, Link, Paper, Typography } from '@mui/material';
import EmailIcon from '@mui/icons-material/Email';
import GitHubIcon from '@mui/icons-material/GitHub';
import { Box } from '@mui/system';



const Redes = () => {
    
    return (  
        <Box
            sx={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                gap: 2,
            }}
        >
            <Typography variant="h5">
                Redes y contacto:
            </Typography>
            <Box
                component={Paper}
                variant="outlined"
                sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    gap: 2,
                    "&:hover": {
                        p: 2.3,
                        boxShadow: 2
                    },
                    transition: 'padding 0.3s',
                    p: 2
                }}
            >
                <EmailIcon />
                <ButtonBase
                    component={Link}
                    href="mailto:rojash.camiloe@gmail.com"
                >
                    <Typography variant="subtitle2" component="div">
                        rojash.camiloe@gmail.com
                    </Typography>
                </ButtonBase>
            </Box>
            <Box
                component={Paper}
                variant="outlined"
                sx={{
                    display: 'flex',
                    flexDirection: 'row',
                    gap: 2,
                    "&:hover": {
                        p: 2.3,
                        boxShadow: 4
                    },
                    transition: 'padding 0.3s',
                    p: 2
                }}
            >
                <GitHubIcon />           
                <ButtonBase
                    component={Link}
                    href="https://github.com/CamiloERH"
                >
                    <Typography variant="subtitle2" component="div">
                        github.com/CamiloERH
                    </Typography>
                </ButtonBase>
            </Box>
        </Box>  
    );
}
 
export default Redes;