import { Paper, Typography } from '@mui/material';
import React from 'react'


const SkillCard = ({imagen, nombre}) => {
    return (  
        <Paper
            variant="outlined"
            sx={{ 
                display: 'flex',
                gap: 2,
                p: 1,
                alignItems: 'center',
                userSelect: 'none'
            }}
        >
            <img
                alt={nombre}
                src={imagen}
                height="45px"
                width="45px"
            />
           
            <Typography variant="h5">
                {nombre}
            </Typography>
        </Paper>
    );
}
 
export default SkillCard;