import React from 'react';

import mui from '../../images/mui.svg';
import mongodb from '../../images/mongodb-icon.svg';
import reactsvg from '../../images/reactsvg.svg'

import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import SkillCard from './SkillCard';


const Skills = () => {

    const skills = [
        {
            nombre: 'Material-UI', 
            imagen: mui
        },
        {
            nombre: 'MongoDB',
            imagen: mongodb
        },
        {
            nombre: 'ReactJS',
            imagen: reactsvg
        }
    ];

    return (  
        <Box
            sx={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                gap: 2,
            }}
        >
            <Typography variant="h5">
                Skills:
            </Typography>
           
            {
                skills.map((skill) => (
                    <SkillCard
                        key={skill.nombre}
                        imagen={skill.imagen}
                        nombre={skill.nombre}
                    />
                ))
            }

        </Box>
    );
}
 
export default Skills;